import React from "react"
import { Helmet } from "react-helmet";
import { Link } from "react-router-dom";

import headerBackground from 'assets/images/vr1.jpg';

export default class Configurator extends React.Component {
    constructor() {
        super();
        this.state = { playButtonVisibility: 'visible' };
    }

    render() {
        return (
            <div className='homepage center center-page-x'>
                <Helmet>
                    <meta charSet="utf-8" />
                    <title>Personalize shopping in 3d and AR</title>
                    <link rel="canonical" href="https://www.ezyvue.com/platform/configurator" />
                </Helmet>
                <section className="full-width-header"
                    style={{
                        backgroundImage: `url(${headerBackground})`,
                        justifyContent: 'left',
                        backgroundPosition: '100%'
                    }
                    }>
                    <div style={{
                        maxWidth: '600px',
                    }}>
                        <h1>App-less web VR for Android devices</h1>
                        <h3>Product visualization, education, architectural design, real estate are some of the applications</h3>
                    </div>
                    <div className='full-width-header-promo-button'>
                        <div className="homepage-button-medium">
                            <Link to='/pricing'>Get started for free</Link>
                        </div>
                    </div>
                </section>
                <section className='homepage-section' style={{ textAlign: 'center' }}>
                    <h1>Virtual Reality</h1>
                    <h4 style={{color: '#888'}}></h4>
                </section>
               
                <section className='homepage-section'>
                    <div className="flexrow">
                        <div className='flexcol'>
                            <h3>Virtual property tours</h3>
                            <p>
                                With immersive VR your customers can virtually view and experience properties in the comfort of their own home. 
                                Take your 360&#176; photos and produce realistic panoramas that can be viewed from your website in 3d and VR.<br/>
                                Compatible with all VR headsets including Google Cardboard and VR Box.  
                            </p>
                            <a href='https://www.ezyvue.com/demos'>Try out the demos</a>           

                        </div>
                        <div className="flexcol"> 
                            <div className='homepage-viewer aspect-three-two' style={{maxWidth: '600px'}}>
                                <div className='aspect-inner'>
                                    <iframe id="ezyvue-viewer"
                                        src={`${process.env.REACT_APP_VIEWER_URL}/viewer?productId=63fc0240294173247367a8c7&playbutton&animateplaybutton&hidemenu&showbackground=true`}
                                        width="100%"
                                        height="100%"
                                        style={{ border: 'none' }}
                                        frameBorder="0"
                                        allow="xr-spatial-tracking"
                                        allowFullScreen={true}
                                        mozallowfullscreen="true"
                                        webkitallowfullscreen="true" />
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
                <section className='homepage-section'>
                    <div className="flexrow">
                        <div className="flexcol">
                            <h3>Taking 360&#176; photos for VR</h3>
                            <p>
                                
                            </p>
                        </div>
                        <div className='flexcol'>
                            <img alt='360 photo' src={require('../../assets/images/360.jpg')} style={{maxWidth: '600px'}} />
                        </div>
                    </div>
                </section>
                {/* <section className='homepage-section'>
                    <div className="flexrow">
                        <div className='flexcol'>
                            <h3>Create multiple component variants</h3>
                            <p style={{ textAlign: 'left' }}>
                                The configurator allows shoppers to choose from available components and features.
                                Separate component models can be swapped in without loading a new model.
                            </p>
                        </div>
                        <div className='flexcol'>
                            <div className='homepage-configurator-aspect homepage-viewer light-shadow'>
                                <div className='aspect-inner'>
                                    <iframe id="ezyvue-viewer"
                                        src={`${process.env.REACT_APP_VIEWER_URL}/viewer?productId=626651cea786f2e2a89170b4&playbutton&animateplaybutton&hidemenu`}
                                        width="100%"
                                        height="100%"
                                        style={{ border: 'none' }}
                                        frameBorder="0"
                                        allow="xr-spatial-tracking"
                                        allowFullScreen={true}
                                        mozallowfullscreen="true"
                                        webkitallowfullscreen="true" />
                                </div>
                            </div>
                        </div>
                    </div>
                </section> */}
                <br />
                <br />
            </div >
        );
    }
}